<template>
  <el-container id="edm-detail-page">
    <div class="main-container">
      <el-button type="success" @click="onChooseCollectionClick">
        Choose Collection
      </el-button>
      <div class="input-container">
        <div class="input-item">
          <p class="label">EDM Subject:</p>
          <el-input v-model="subject" clearable> </el-input>
        </div>
        <div class="input-item">
          <p class="label">Message to Receiver:</p>
          <el-input
            type="textarea"
            rows="2"
            v-model="messageBody"
            clearable
            placeholder="Message to Receiver"
          >
          </el-input>
        </div>
        <div class="input-item">
          <p class="label">EDM Cover Photo:</p>
          <el-button type="success" @click="onUploadClick">Upload</el-button>
          <div class="note-container">
            <p class="note">
              Recommended aspect ratio of the cover is 3:2;
            </p>
            <p class="note">
              Perfect size: length 592px and width 392px
            </p>
          </div>
        </div>
        <div class="input-item">
          <p class="label">EDM Description:</p>
          <el-input type="textarea" rows="3" v-model="message" clearable>
          </el-input>
        </div>
      </div>

      <p class="preview">Preview</p>

      <div class="text-container">
        <p v-for="(msg, index) in messageBody.split(/\n/)" :key="index">
          {{ msg || "&nbsp;" }}
        </p>
      </div>

      <div class="cover-container">
        <div v-if="coverPhoto" class="template-container">
          <img
            class="edm-cover-image"
            :src="formatImageUrl(coverPhoto.file)"
            alt="cover"
          />
        </div>
      </div>

      <div class="text-container">
        <p v-for="(msg, index) in message.split(/\n/)" :key="index">
          {{ msg || "&nbsp;" }}
        </p>
      </div>

      <div class="fabrics-container" v-if="collections.length">
        <el-row>
          <el-col v-for="(item, index) in collections" :key="index" :span="12">
            <div class="card">
              <div
                class="image"
                :style="{
                  backgroundImage: `url('${formatImageUrl(
                    item.fabric.image_url
                  ) + '@150-150'}')`
                }"
              ></div>
              <div class="right-container">
                <h3>{{ item.fabric.name }}</h3>
                <p>{{ item.fabric.real_composition }}</p>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>

      <div class="button-container">
        <el-button type="info" @click="onCancelClick">
          Cancel
        </el-button>
        <el-button type="success" @click="onSaveClick">
          Save
        </el-button>
      </div>
    </div>

    <EDMDialog ref="dialog" @select="onDialogSelect"></EDMDialog>
  </el-container>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  toRefs,
  computed,
  onMounted,
  Ref,
  watchEffect
} from "vue";
import { useStore } from "vuex";

import EDMDialog from "./EDMDialog.vue";
import { GetEdm, PostUpdateEdm, GetCollection } from "@/typings/store";
import { formatImageUrl, loading } from "@/utils";
import { EdmCover, EdmParams } from "@/typings/edm";
import { ElNotification } from "element-plus";
import { iCreateDialog } from "@/typings/component";
import { createEDMHtml } from "@/utils/createEDMHtml";
import { useRouter } from "vue-router";
import { postCreateCover } from "@/apis/edm";
import { getAvaliableOptions } from "@/apis/project";
import { OptionItem } from "@/typings/asideFilter";

const Component = defineComponent({
  name: "EDMDetail",
  components: {
    EDMDialog
  },
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  setup: function(props) {
    const store = useStore();
    const router = useRouter();
    const dialog: Ref<iCreateDialog | null> = ref(null);

    const { id } = toRefs(props);
    const subject = ref("");
    const brandName = ref("");
    const message = ref("");
    const messageBody = ref("");
    const collectionId = ref<number | null>(null);
    const collections = ref([]);
    const coverPhoto = ref<EdmCover | null>(null);

    const detail = computed(() => store.state.edm.detail);
    const tempCollections = computed(() => store.state.collection.detail);
    const brandOptions = ref<OptionItem[]>([]);

    const fetchCollection = async (id: number) => {
      await store.dispatch(GetCollection, id);
      collections.value = tempCollections.value.collection_fabrics;
    };

    watchEffect(() => {
      console.log(messageBody.value.split(/\n/));
    });

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetEdm, id.value);
        subject.value = detail.value.subject;
        brandName.value = detail.value.recipient_brand;
        message.value = detail.value.message;
        messageBody.value = detail.value.message_body;
        coverPhoto.value = detail.value.cover_photo;
        collectionId.value = detail.value.chosen_collection;

        const brandResponse = await getAvaliableOptions("brand");
        brandOptions.value = brandResponse.data.brands.map(
          (it: { name: string; id: number }): OptionItem => ({
            label: it.name,
            value: it.id,
            checked: false
          })
        );

        if (collectionId.value) {
          await fetchCollection(collectionId.value);
        }
      } catch (_) {
        await router.replace({ name: "Error" });
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onChooseCollectionClick = () => {
      if (!dialog.value) return;
      dialog.value.open();
    };

    const onUploadClick = () => {
      const element = document.createElement("input");
      element.type = "file";
      element.accept = "image/*";
      element.addEventListener("change", async () => {
        if (element.files) {
          const { data } = await postCreateCover(element.files[0]);
          coverPhoto.value = data;
        }
      });
      element.click();
    };

    const onDialogSelect = async (id: number) => {
      collectionId.value = id;
      await fetchCollection(id);
    };

    const onCancelClick = async () => {
      await router.back();
    };

    const onSaveClick = async () => {
      const params: EdmParams = {
        subject: subject.value,
        message: message.value,
        message_body: messageBody.value,
        html_message: createEDMHtml(
          coverPhoto.value,
          collections.value,
          messageBody.value,
          brandName.value,
          message.value,
          collectionId.value
        ),
        recipient_brand: brandName.value,
        chosen_collection: collectionId.value
      };
      if (coverPhoto.value) {
        params.cover_photo = coverPhoto.value.id;
      }
      const data = await store.dispatch(PostUpdateEdm, {
        id: id.value,
        params
      });
      ElNotification({
        title: "Success",
        type: "success",
        message: `Save EDM ${data.subject} successfully.`,
        customClass: "message-box"
      });
      await router.push({ name: "EDMDisplay", params: { id: id.value } });
    };

    return {
      dialog,
      subject,
      brandName,
      message,
      messageBody,
      collections,
      collectionId,
      coverPhoto,
      detail,
      brandOptions,

      onChooseCollectionClick,
      onUploadClick,
      onDialogSelect,
      onCancelClick,
      onSaveClick,
      formatImageUrl
    };
  }
});

export default Component;
</script>

