import { CollectionFabrics } from "@/typings/collection";
import { formatImageUrl } from "@/utils/image";
import { EdmCover } from "@/typings/edm";

const createImage = (data: CollectionFabrics | undefined) => {
  if (!data) return "";
  const domain = `${process.env.VUE_APP_DOMAIN}/web/fabric/`;
  const url = encodeURI(data.fabric.image_url);
  return `
  <table
    border="0"
    cellpadding="0"
    cellspacing="0"
    width="100%"
  >
    <tr>
      <td width="140" valign="top">
        <a href="${domain}${data.fabric.id}">
          <img
            src="${formatImageUrl(url) + "@140-140"}"
            alt="${data.fabric.name}"
            width="140"
            height="140"
            style="display: block;"
          />
        </a>
      </td>
      <td
        style="font-size: 0; line-height: 0;"
        width="15"
      >
        &nbsp;
      </td>
      <td width="156">
        <a 
          href="${domain}${data.fabric.id}" 
          style="color: black; text-decoration: none;"
        >
          <h3 style="font-size: 18px; font-weight: 700;">
            ${data.fabric.name}
          </h3>
          <p style="font-size: 14px; font-weight: 600;">
            ${data.fabric.real_composition}
          </p>
        </a>
      </td>
    </tr>
  </table>
`;
};

const createCover = (coverPhoto: EdmCover | null) => {
  return `
  <table
    align="center"
    border="0"
    cellpadding="0"
    cellspacing="0"
    width="100%"
  >
    ${
      coverPhoto
        ? `
        <tr>
          <td style="font-size: 0; line-height: 0;" height="4">
            &nbsp;
          </td>
        </tr>
        <tr>
          <td width="592" valign="top" style="padding-right: 4px; padding-left: 4px;">
            <img
              src="${formatImageUrl(coverPhoto.file) + "@592-392"}"
              alt=""
              width="592"
              height="392"
              style="display: block;"
            />
          </td>
        </tr>
        <tr>
          <td style="font-size: 0; line-height: 0;" height="4">
            &nbsp;
          </td>
        </tr>
      `
        : ""
    }
    <tr>
      <td style="font-size: 0; line-height: 0;" height="">
        &nbsp;
      </td>
    </tr>
  </table>
  `;
};

const createCollections = (collections: CollectionFabrics[]) => {
  const data = collections.reduce((prev, item, index) => {
    if (index % 2) {
      // @ts-ignore
      (prev[prev.length - 1] as []).push(item);
    } else {
      // @ts-ignore
      prev.push([item]);
    }
    return prev;
  }, []);

  return `
    <table
      border="0"
      cellpadding="0"
      cellspacing="0"
      width="100%"
    >
      ${data.reduce((str, item) => {
        return (
          str +
          `
          <tr>
            <td width="296" valign="top">
              ${createImage(item[0])}
            </td>
            <td style="font-size: 0; line-height: 0;" width="8">
              &nbsp;
            </td>
            <td width="296" valign="top">
              ${createImage(item[1])}
            </td>
          </tr>
            <td style="font-size: 0; line-height: 0;" height="15">
              &nbsp;
            </td>
          <tr>
        `
        );
      }, "")}
    </table>
  `;
};

export const createEDMHtml = (
  coverPhoto: EdmCover | null,
  collections: CollectionFabrics[] | undefined,
  text: string,
  brandName: string,
  message: string,
  collectionId: number | null
) => {
  const collectionUrl = `${process.env.VUE_APP_DOMAIN}/web/collection/${collectionId}`;
  return `
    <!DOCTYPE html PUBLIC "-//W3C//DTD XHTML 1.0 Transitional//EN" "http://www.w3.org/TR/xhtml1/DTD/xhtml1-transitional.dtd">
      <html xmlns="http://www.w3.org/1999/xhtml">
　      <head>
　　      <meta http-equiv="Content-Type" content="text/html; charset=UTF-8" />
　　        <title>${brandName}</title>
　　      <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
　      </head>
        <body style="margin: 0; padding: 0;">
          <table border="0" cellpadding="0" cellspacing="0" width="100%">
　　        <tr>
              <table
                align="center"
                border="0"
                cellpadding="0"
                cellspacing="0"
                width="600"
              >
                <tr>
                  <td
                    style="padding-top: 30px; padding-right: 15px; padding-bottom: 30px; padding-left: 15px; background-color: #f7f7f7;"
                  >
                    ${text.split(/\n/).reduce((str, item) => {
                      return (
                        str +
                        `<p style="font-size: 16px; font-weight: 500; color: #2c3e50; margin-top: 0; margin-bottom: 0; ">
                          ${item || "&nbsp;"}
                        </p>`
                      );
                    }, "")}
                  </td>
                </tr>
                <tr>
                  <td style="font-size: 0; line-height: 0;" height="30">
                    &nbsp;
                  </td>
                </tr>
                <tr>
                  <td
                    style="padding-top: 0; padding-right: 0; padding-bottom: 0; padding-left: 0; background-color: #f7f7f7;"
                  >
                   ${createCover(coverPhoto)}
                   </td>
                 </tr>
                 <tr>
                   <td style="font-size: 0; line-height: 0;" height="30">
                     &nbsp;
                   </td>
                 </tr>
                <tr>
                  <td
                    style="padding-top: 30px; padding-right: 15px; padding-bottom: 30px; padding-left: 15px; background-color: #f7f7f7;"
                  >
                    ${message.split(/\n/).reduce((str, item) => {
                      return (
                        str +
                        `<p style="font-size: 16px; font-weight: 500; color: #2c3e50; margin-top: 0; margin-bottom: 0;">
                          ${item || "&nbsp;"}
                        </p>`
                      );
                    }, "")}
                  </td>
                </tr>
                <tr>
                  <td style="font-size: 0; line-height: 0;" height="30">
                    &nbsp;
                  </td>
                </tr>
                ${
                  collections
                    ? `
                      <tr>
                        <td style="padding-top: 30px; padding-right: 4px; padding-left: 4px">
                          ${createCollections(collections)}
                        </td>
                      </tr>
                    `
                    : ""
                }
                <tr>
                  <td>
                    <p style="padding-top: 30px; font-size: 16px;">Go to our website for more information!</p>
                  </td>
                </tr>
                <tr>
                  <td>
                    <a href="${collectionUrl}">
                      ${collectionUrl}
                    </a>
                  </td>
                </tr>
              </table>
　　        </tr>
          </table>
        </body>
     </html>
  `;
};
